/* Tables
   ========================================================================== */
/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Ubuntu", sans-serif; }

.inner {
  width: 90%;
  margin-right: auto;
  margin-left: auto; }
  .inner:after {
    clear: both;
    content: "";
    display: table; }
  .inner:before {
    content: "";
    display: table; }

.dhr-main {
  min-height: 60vh; }

.dhr-preheadline {
  text-transform: uppercase;
  color: #2bace9;
  font-size: .9em;
  font-family: "Proxima Nova", sans-serif; }

a.dhr-button {
  display: inline-block;
  padding: .75em 1.5em;
  background: #ffa700;
  color: #292b33;
  font-size: .9em;
  border-radius: 5px; }

.dhr-header > .inner {
  width: 100%; }

.dhr-header--brand {
  padding: 1em 0; }
  .dhr-header--brand:after {
    clear: both;
    content: "";
    display: table; }
  .dhr-header--brand:before {
    content: "";
    display: table; }
  .dhr-header--brand a {
    width: 11em;
    float: left; }

.dhr-header--navtoggle {
  display: block;
  width: 4em;
  float: right;
  text-align: right; }

.dhr-header--menu {
  background: #292b33; }
  .dhr-header--menu ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .dhr-header--menu ul > li {
      display: block;
      text-align: center; }
      .dhr-header--menu ul > li a {
        display: block;
        color: #fff;
        padding: .6em;
        border-top: 1px solid #34363d;
        transition: background .3s ease; }
        .dhr-header--menu ul > li a:hover, .dhr-header--menu ul > li a:active {
          background: #34363d; }
  .dhr-header--menu .dhr-button {
    border-radius: 0;
    width: 100%;
    text-align: center; }

@media (min-width: 767px) {
  .dhr-header {
    padding: 1em 0; }
    .dhr-header > .inner {
      width: 94%; }
  .dhr-header--brand {
    padding: .25em 0 0;
    width: 35%;
    float: left; }
    .dhr-header--brand a {
      width: 13em; }
  .dhr-header--navtoggle {
    display: none; }
  .dhr-header--menu {
    background: transparent;
    position: relative;
    width: 65%;
    float: right;
    text-align: right;
    display: block !important; }
    .dhr-header--menu ul {
      padding-right: 12em;
      padding-top: .45em; }
    .dhr-header--menu ul > li {
      display: inline-block; }
      .dhr-header--menu ul > li a {
        padding: .2em;
        border: none; }
        .dhr-header--menu ul > li a:hover {
          background: transparent;
          color: #ffa700; }
    .dhr-header--menu .dhr-button {
      width: auto;
      position: absolute;
      right: 0;
      top: 0;
      border-radius: 5px; } }

@media (min-width: 992px) {
  .dhr-header {
    padding: 1.2em 0; }
  .dhr-header--brand {
    padding-top: 0.1em; }
    .dhr-header--brand a {
      width: 14em; }
  .dhr-header--menu ul {
    padding-right: 13em; }
  .dhr-header--menu ul > li {
    margin-left: 1em; }
    .dhr-header--menu ul > li:first-child {
      margin-right: 0; } }

@media (min-width: 1199px) {
  .dhr-header {
    padding: 1.5em 0; }
  .dhr-header--menu ul > li {
    margin-left: 1.5em; } }

.dhr-form--inner {
  max-width: 992px; }

.dhr-formfield input[type="email"], .dhr-formfield input[type="text"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  outline: none;
  border: none;
  padding: 0;
  background: transparent;
  text-align: center;
  display: block;
  width: 100%;
  border: none;
  border-bottom: 2px solid #3e4047; }
  .dhr-formfield input[type="email"]::-webkit-input-placeholder, .dhr-formfield input[type="text"]::-webkit-input-placeholder {
    color: #494b52; }
  .dhr-formfield input[type="email"]::-moz-placeholder, .dhr-formfield input[type="text"]::-moz-placeholder {
    color: #494b52; }
  .dhr-formfield input[type="email"]:-ms-input-placeholder, .dhr-formfield input[type="text"]:-ms-input-placeholder {
    color: #494b52; }
  .dhr-formfield input[type="email"]:-moz-placeholder, .dhr-formfield input[type="text"]:-moz-placeholder {
    color: #494b52; }

.dhr-footer--content {
  padding: 3em 0;
  text-align: center; }

.dhr-footer--headline h2 {
  color: #fff; }

.dhr-footer--form {
  padding: 2em 0 3em; }
  .dhr-footer--form input[type="email"], .dhr-footer--form input[type="text"] {
    height: 2em;
    font-size: 2em; }
